<script>
export default {
  name: 'Home',
  data() {
    return {
      activeMenu: ''
    }
  },
  computed: {
    currentPath() {
      return this.$route.path
    }
  },
  created() {
    this.activeMenu = this.currentPath
  },
  methods: {
    handleSelect(key) {
      this.activeMenu = key
    },
    handleLogout() {
      this.$confirm('确定要退出登录吗？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$store.dispatch('logout')
        this.$router.push('/login')
        this.$message.success('已退出登录')
      }).catch(() => {})
    }
  },
  watch: {
    currentPath(newPath) {
      this.activeMenu = newPath
    }
  }
}
</script>

